@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.login-body {
	border-radius: 6px;
	background-color: gainsboro;
	padding: 2rem;
	height: 725px;
}
.login-footer {
	background-color: gainsboro;
}
.login-img {
	background-image: url("/img/books.jpg");
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
}
.login-footer button {
	margin: 20px;
}
form {
  width: 100%;
  max-width: 400px;
  margin: 0 auto 0;
}

input {
  display: block;
  margin: 2rem auto 0;
  font-size: 1.0rem;
  padding: 0.25rem 0.5rem;
  border: none;
  border-radius: 6px;
  width: 100%;
}

label {
  display: block;
  margin: 0.5rem 0 0;
  font-size: 0.9rem;
  text-align: left;
  padding-left: 0.5rem;
}



.add-new-modal {
	background-color: rgba(0, 0, 0, 0.7);
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	display: flex;
	justify-content: center;
	align-items: center;
}
.add-new-wrapper {
	width: 50%;
	max-width: 500px;
	border-radius: 6px;
	background-color: gainsboro;
	padding: 2rem;
}
.MuiButton-containedPrimary {
	margin: 1em;
}
.add input .cancel input {
	display: none;
}
.add:focus .cancel:focus {
	box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, .5);
}
button.MuiButtonBase-root.MuiButton-root.MuiButton-contained.MuiButton-containedPrimary.cancel {
	margin: 1.5em !important;
}
button.MuiButtonBase-root.MuiButton-root.MuiButton-contained.MuiButton-containedPrimary.add {
	margin: 1.5em !important
}
.MuiInputBase-input {
	width: 100%;
}
.add-new-wrapper .MuiInputBase-root {
	width: 100% !important;
	margin-top: 16px;
	margin-bottom: 8px;
}
.MuiFormControl-marginNormal {
	width: 100%;
}
.show-more-modal {
	background-color: rgba(0, 0, 0, 0.7);
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	display: flex;
	justify-content: center;
	align-items: center;
}
.show-more-wrapper {
	width: 50%;
	max-width: 500px;
	border-radius: 6px;
	background-color: gainsboro;
	padding: 2rem;
}
button.MuiButtonBase-root.MuiButton-root.MuiButton-contained.MuiButton-containedPrimary.more {
	margin-right: 10px;
}
.MuiList-padding {
	margin-left: 85px !important;
	padding: 30px !important;
}
.close {
	text-align: right;
}
.book-list {
	padding: 20px 0 6rem;
	font-family: 'Roboto', sans-serif;
}
.book-list h2 {
	text-align: left;
	font-size: 2.2rem;
	color: #000;
	padding-left: 20px;
}
h6 {
	color: #000;
	text-transform: uppercase;
	font-weight: 700;
	text-align: left;
}
.MuiTypography-subtitle1 {
	line-height: 1 !important;
}
.bookicn {
	font-size: 70px;
	font-weight: bold;
}
.addbtn {
	text-align: left;
	margin: 15px;
}
.library {
	border-radius: 6px;
	padding: .5rem;
	margin: 15px;
	height: 75px;
}
.library p, .library time {
	color: #000;
	font-size: .75rem;
	display: inline-block;
	text-align: left;
}
.title {
	color: #000;
	text-transform: uppercase;
	font-weight: bold !important;
}
.largeIcon {
	font-size: 60px;
	color: #dc3545;
}
.author {
	color: #000;
	text-transform: capitalize;
}
.remove {
	text-align: right;
}
header {
  background-color: #343738;
  font-size: calc(10px + 2vmin);
  color: white;
  text-align: right;
}

.header-wrapper {
  width: 100%;
  max-width: 1024px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.login {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  padding: 0;
  border: none;
  color: white;
  background-color: transparent;
  font-size: 1rem;
  cursor: pointer;
}
.login:hover,
.login:focus {
  text-decoration: underline;
}

.header-wrapper p {
  font-size: 1rem;
}
body{background-color:#f0f3f8}.app{text-align:center}.wrapper{width:100%;max-width:1024px;margin:0 auto}
